const app = {
  state: {
    navIndex: 0
  },
  mutations: {
    CHANGE_NAV_ACTIVE: (state, navIndex) => {
      state.navIndex = navIndex
    }
  },
  actions: {
    ChangeNavIndex: ({commit}, val) => {
      commit('CHANGE_NAV_ACTIVE', val)
    }
  }
}
export default app



